<template>
  <section class="new-order-container">
      <el-button style="margin-left:40%; margin-top: 20%" @click = "onNewOrder()" >新开单</el-button>

    <!--新增界面-->

  </section>
</template>

<script>
  export default {
    methods:{
      onNewOrder(){
        this.addFormVisible = true;
        alert("敬请期待")
      }
    },
    data(){
      return {
        addFormVisible: false,//新增界面是否显示
        addProductForm: {
          id: null,
          productCode: null,
          name: '',
          originalPrice: null,
          price: null,
          pictureUrl: '',
          sales: null,
          sortValue: 0,
          summary: '',
          type: null,
        },
        addFormRules: {
          id: [
            {required: true, validator: validateID, trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入商品名称', trigger: 'blur'},
          ],
          price: [
            {required: true, message: '请输入商品价格', trigger: 'blur'},
          ],
        },
      }
    }
  }
</script>



